@use "src/assets/styles/variables" as var;

.transport-info {
  display: flex;
  gap: 4px;

  .info-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .number {
    font-size: 17px;
    font-weight: 700;
  }

  .route {
    color: var.$pale;
    font-size: 14px;
    line-height: 15.5px;
    text-align: left;
  }

  svg {
    margin-top: 6px;
  }
}
