// colors
$default: #fff;
$blue: #005eff;
$green: #0fba00;
$default-inverted: #000;

// radius
$border-radius-s: 5px;
$border-radius-m: 10px;
$border-radius-l: 20px;
$border-radius-xl: 40px;
$border-radius-circle: 50%;

// logical colors
$static: $default;
$text-accent: $green;
$text-accent-2: $blue;
$text-main: $default-inverted;
$pale: rgba($default-inverted, 0.5);
$text-pale: rgba($default-inverted, 0.57);
$text-pale-2: rgba($default-inverted, 0.43);
// background colors
$dark-background: $default-inverted;
$accent-background: $blue;
$pale-accent-background: rgba($blue, 0.57);
$blurred-background: rgba($default-inverted, 0.8);

$menu-bar-height: 84px;
