@use "src/assets/styles/variables" as var;

.button {
  display: inline-flex;
  width: 100%;
  padding: 11px 12px;
  border: none;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  outline: none;
  cursor: pointer;
  letter-spacing: 0.64px;
  transition: 0.3s linear;

  .loader {
    font-size: 16px;
    display: inline-block;
    font-weight: 500;
    color: var.$text-main;
    text-shadow: 0 0 2px var.$static, 0 0 1px var.$static, 0 0 1px var.$static;
    letter-spacing: 2px;
  }

  .loader::after {
    content: "Loading";
    position: absolute;
    left: 0;
    top: 0;
    color: var.$static;
    width: 100%;
    height: 100%;
    overflow: hidden;
    box-sizing: border-box;
    animation: animloader 6s linear infinite;
  }

  &_primary {
    color: var.$static;
    border-radius: var.$border-radius-m;
    background: var.$accent-background;
  }

  &_secondary {
    color: var.$text-accent-2;
    border-radius: var.$border-radius-s;
    background: rgba(var.$accent-background, 0.5);
  }

  &_s {
    width: 145px;
    height: 36px;
    font-size: 12px;
  }

  &_m {
    width: 236px;
    height: 48px;
    font-size: 21px;
    font-weight: 700;
  }

  &:disabled {
    cursor: auto;
  }

  &:hover:not(&:disabled) {
    color: var.$text-main;
  }
}

a.button {
  line-height: 1rem;
}

@keyframes animloader {
  0% {
    width: 0%;
  }
  100% {
    width: 100%;
  }
}
