.schedule{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    .link-to-home{
        color: rgba(0, 0, 0, 0.50);
        text-align: center;
        font-family: Lato;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 21.257px; 
        letter-spacing: -0.394px;
        text-decoration: none;
        span{
            margin-right: 10px;
        }
    }
    .location{
        margin-top: 13px;
        color: #000;
        text-align: center;
        font-style: normal;
        font-size: 20px;
        line-height: 32px; /* 160% */
        font-weight: 700;
        letter-spacing: -0.593px; 
        span{
            color: #005CFF;
        }
    }
    .types{
        margin-top:20px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        h1{
            color: #000;
            text-align: center;
            font-style: normal;
            font-size: 16.426px;
            line-height: 21.257px;
            font-weight: 400;
            letter-spacing: -0.394px; 
            margin-bottom: 8px;
        }
        .numbers{
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            align-items: flex-start;
        }
        .number{
            width: 38px;
            flex-shrink: 0; 
            height: 38px;
            background: #FFF; 
            border-radius: 5px;
            box-shadow: 1px 3px 1px rgba(0, 0, 0, 0.20);
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 16px;
            margin-right: 8px;
            span{
                color: #000;
                font-size: 14px;
                line-height: 21.257px;
                font-weight: 400;
                letter-spacing: -0.394px; 
            }    
        }
        .number + .number{
            
        }
    }
}