@use "src/assets/styles/variables" as var;

.route-builder {
  width: 90%;
  display: flex;
  padding: 12px 22px 12px 8px;
  color: var.$pale;
  align-items: center;
  border: 1px solid var.$pale;
  font-size: 16px;
  border-radius: var.$border-radius-m;
  cursor: text;

  .marker {
    margin-right: 16px;
    cursor: pointer;
  }

  .input {
    width: 100%;
    color: var.$text-main;
    border: none;
    background: transparent;
    outline: none;
  }

  svg {
    cursor: pointer;
  }
}
