@use "src/assets/styles/variables" as var;

.menu-bar {
  position: fixed;
  bottom: -20px;
  height: var.$menu-bar-height;
  background: var.$static;

  .menu-list {
    display: flex;
    padding: 12px 18px 32px;
    gap: 0 32px;
    z-index: 2;

    .nav-item {
      list-style: none;
    }

    .link {
      font-family: "Inter", sans-serif;
      display: inline-flex;
      flex-direction: column;
      align-items: center;
      color: var.$text-pale;
      text-decoration: none;
      font-weight: 500;
      line-height: 21px;
    }
  }
}
