@use "src/assets/styles/variables" as var;

.second-screen {
  .text {
    &::after {
      position: absolute;
      content: "";
      top: 100%;
      left: 50%;
      width: 100%;
      height: 2px;
      background: var.$dark-background;
      transform: translate(-50%, 15px);
    }
  }

  .popular {
    margin-top: 30px;
  }

  .transport-types {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 8px;
  }

  .transport {
    width: 48%;
    height: 100px;
    padding: 12px 16px;
    border-radius: var.$border-radius-l;
    background: var.$static;
    letter-spacing: -0.394px;
    box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.25);
    display: flex;
    flex-direction: column;
    position: relative;

    &_img {
      width: 56px;
      position: absolute;
      bottom: 0;
    }
  }

  .text-accent {
    font-size: 17px;
    color: var.$pale;
    font-weight: 700;
    text-align: left;
  }

  .routes {
    margin-top: 20px;
  }

  .state-manually {
    margin-top: 6px;
    padding: 19px 16px;
    border-radius: var.$border-radius-m;
    border: 1px solid var.$pale-accent-background;
    background: var.$static;
    box-shadow: 0px -1px 4px 0px var.$pale-accent-background,
      0px 1px 4px 0px var.$pale-accent-background;
  }
}
