@use "fonts.scss";
@use "variables.scss" as var;

* {
  position: relative;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,
body {
  max-width: 100vw;
  overflow-x: hidden;
  background: var.$static;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 14px;
  color: var.$text-main;
  line-height: 32px;
}

h1,
h2,
h3 {
  font-weight: 700;
  text-align: center;
  line-height: 32px;
  letter-spacing: -0.593px;
}

h2,
h3 {
  margin-bottom: 13px;
}

h1 {
  font-size: 25px;
}

h2 {
  font-size: 19px;
}

h3 {
  font-size: 14px;
}

.marked {
  text-align: center;
  font-size: 20px;
  font-weight: 600;
}

p {
  text-align: center;
}
