.explore{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    h1{
        color: #000;
        text-align: center;
        font-size: 20px;
        font-weight: 700;
        line-height: 32px; 
        letter-spacing: -0.593px;
        margin-bottom: 35px;
        span{
            color: #005CFF; 
        }
    }
    .content{
        display: flex;
        flex-direction: row;
    }
    .left-side{
        div{
            border-radius: 15px;
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
            box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.25);
            width: 164px;
            height: 212px;
            flex-shrink: 0;
            margin-bottom: 30px;
            position: relative;
            div{
                width: 164px;
                border-radius: 15px;
                height: 70px; 
                backdrop-filter: blur(2px); 
                background: rgba(255, 255, 255, 0.49);
                position: absolute;
                bottom: -30px;
                display: flex;
                justify-content: center;
                align-items: center;
                h1{
                    color: #000;
                    text-align: center;
                    font-size: 12px;
                    line-height: 22px;
                    font-weight: 400;
                    letter-spacing: -0.408px;
                    padding-top: 30px ;
                }
            }
        }
        .card1{
            background-image: url(./images/card1.png);
        }
        .card2{
            background-image: url(./images/card2.png);
        }
        .card3{
            background-image: url(./images/card3.png);
        }
        
    }
    .right-side{
        margin-left: 30px;
        .open-map{
            display: flex;
            height: 48px;
            width: 164px;
            justify-content: center;
            padding: 0px 12px;
            gap: 4px;
            align-items: center;
            flex-shrink: 0; 
            border-radius: 10px;
            background: #005CFF; 
            color: white;
            font-size: 12px;
            font-weight: 400;
            box-shadow: 0px 4px 8px 0px rgba(101, 92, 128, 0.30); 
            margin-bottom: 30px;
        }
        div{
            border-radius: 15px;
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
            box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.25);
            width: 164px;
            height: 212px;
            flex-shrink: 0;
            margin-bottom: 30px;
            position: relative;
            div{
                width: 164px;
                border-radius: 15px;
                height: 70px; 
                backdrop-filter: blur(2px); 
                background: rgba(255, 255, 255, 0.49);
                display: flex;
                align-items: center;
                justify-content: center;
                position: absolute;
                bottom: -30px;
                h1{
                    color: #000;
                    font-size: 12px;
                    line-height: 22px;
                    font-weight: 400;
                    letter-spacing: -0.408px;
                    padding-top: 30px;
                }
            }
        }
        .card4{
            background-image: url(./images/card4.png);
        }
        .card5{
            background-image: url(./images/card5.png);
        }
        .card6{
            background-image: url(./images/card6.png);
        }
    }
}